h1,
p {
  margin: 0.25em;
}

p {
  font-size: 0.75em;
}

.meme {
  flex-direction: column;
  align-content: center;
}

.meme img {
  width: 20em;
  margin: 0.5em;
}

.meme button {
  width: 50%;
  height: 3em;
  margin: 1em;
  border-radius: 15px;
  font-weight: 500;
  border-color: black;
  background-color: white;
  color: #404040;
  font: inherit;
}

button h3 {
  margin: 0;
}

.meme button:hover {
  background-color: rgb(47, 66, 90);
  color: white;
}
