.icon img {
  width: 3em;
}

.container {
  position: relative;
  display: flex;
  justify-content: center;
}

.container button {
  margin-top: 1em;
  margin: 1em 1em 0 1em;
  padding: 0.6em;
  z-index: 10;
  border-radius: 25px;
  border-color: black;
  background-color: white;
}

.container button:hover {
  background-color: rgb(47, 66, 90);
}
