.form {
  text-align: center;
  max-width: 45rem;
  width: 100%;
  color: white;
  padding: 1rem;
  /* box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25); */
}

.form input {
  width: 50%;
  height: 3em;
  margin: 0.75em;
  border-radius: 10px;
}

.form input[type="text"]:focus {
  background-color: lightblue;
}

.submit {
  cursor: pointer;
  font: inherit;
  border: none;
  background-color: white;
  color: #404040;
  padding: 0.5rem 0.5rem;
  margin: 0.75rem;
  border-radius: 25px;
  font-weight: bold;
}
